.navigation {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.navigation .navigation-arrows button {
    margin-right: 30px;
    background-color: inherit;
    outline: none;
    border: none;
    color: white;
}

.navigation .navigation-arrows button:hover {
    color: #e83e8c;
}

.navigation-currMonth button {
    margin-left: 30px;
    background-color: inherit;
    color: whitesmoke;
    border-radius: 20px;
    /*border: 1px solid #80bdff;*/
    padding: 5px;
    outline: none;
}

.navigation-currMonth button:hover {
    box-shadow: 0px 0px 19px -3px rgba(247, 230, 247, 1);
}
.app {
    height: 450px;
    width: 550px;
    padding: 10px;
    margin-left: auto;
    margin-right: auto;
    background-color: #424240;
    border-radius: 20px;
    position: relative;
    box-sizing: border-box;
}
.dayCell {
    -webkit-flex: 1 1 14.2%;
            flex: 1 1 14.2%;
    width: 14.2%;
    height: 14.2%;
    margin-bottom: 5px;
}

.dayCell-wrapper {
    width: 90%;
    height: 50px;
    margin-left: auto;
    margin-right: auto;
    color: white;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    box-sizing: border-box;
    background: inherit;
    border: none;
}

.dayCell-wrapper:hover {
    border: 2px solid #8e908c;
    background-color: #828c95;
}

.dayCell.todayCell .dayCell-wrapper {
    background-color: #0078D7;
    padding: 2px;
}

.dayCell .dayCell-wrapper .dayCell-date {
    position: relative;
}

.dayCell .dayCell-wrapper .dayCell-date .dayCell-icon {
    display: inline-block;
    position: absolute;
    top: -13px;
    right: -15px;
    /*color: ghostwhite;*/
    color: #e83e8c;
}

.dayCell.inactiveCell .dayCell-wrapper {
    color: #6c757d;
}
.calendarGrid {
    display: -webkit-flex;
    display: flex;
    box-sizing: border-box;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}

.dayOfWeek {
    -webkit-flex: 1 1 14.2%;
            flex: 1 1 14.2%;
    text-align: center;
    color: whitesmoke;
}
.monthCell {
    -webkit-flex: 1 1 25%;
            flex: 1 1 25%;
    margin-bottom: 5px;
}

.monthCell-wrapper {
    width: 90%;
    height: 120px;
    margin-left: auto;
    margin-right: auto;
    color: white;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    box-sizing: border-box;
    background: inherit;
    border: none;
}

.monthCell-wrapper:hover {
    border: 2px solid #8e908c;
    background-color: #828c95;
}

.monthCell.todayCell .monthCell-wrapper {
    background-color: #0078D7;
    padding: 2px;
}

.monthsGrid {
    display: -webkit-flex;
    display: flex;
    box-sizing: border-box;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}
.singleDay .addNote {
    position: absolute;
    box-sizing: border-box;
    bottom: 20px;
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}

.singleDay .addNote textarea {
    width: 75%;
    padding: 10px;
    border-radius: 20px;
    outline: none;
}

.singleDay .addNote textarea:focus {
    box-shadow: 0px 0px 9px 1px rgba(232, 62, 140, 1);
}

.singleDay .addNote button {
    width: 50px;
    padding: 5px;
    border-radius: 50%;
    margin-left: 10px;
    color: white;
    background-color: inherit;
    border: none;
    outline: none;
}

.singleDay .addNote button:hover {
    color: #e83e8c;
}

.noteList {
    max-height: 300px;
    overflow: auto;
}

.noteList::-webkit-scrollbar {
    width: 10px;
}

.noteList::-webkit-scrollbar-track {
    background: #818182;
    border-radius: 10px;
}

.noteList::-webkit-scrollbar-thumb {
    background: #adb5bd;
    border-radius: 10px;
}

.noteList::-webkit-scrollbar-thumb:hover {
    background: #bfd9d1;
}
.todoItem {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}
.todoItem-text {
    width: 70%;
    background-color: whitesmoke;
    border-radius: 20px;
    margin-bottom: 15px;
    padding: 5px;
    position: relative;
}

.todoItem-text:before {
    content: "";
    display: block;
    position: absolute;
    width: 10px;
    height: 10px;
    left: -25px;
    top: 9px;
    border-radius: 50%;
    background-color: #b3d7ff;
}

.todoItem-remove button {
    width: 50px;
    background-color: inherit;
    border: none;
    color: whitesmoke;
    outline: none;
}

.todoItem-remove button:hover {
    color: #e83e8c;
}
